            </v-btn>
            <v-btn small text @click="prevStep()">
              Anterior
            </v-btn>
          </v-stepper-content>
          <v-stepper-content step="3" class="px-3">
            <v-card-text class="text-center pt-1 pb-0 px-0">
              <v-select
