              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="datosCita.fecha"
                    hint="MM/DD/YYYY format"
                    label="Seleccione la fecha a consultar"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="datosCita.fecha"
                  @input="getCitasDisponibles()"
                ></v-date-picker>
              </v-menu>
              <v-btn small text @click="prevStep()">
                Anterior
              </v-btn>
              <v-list dense>
                <p
                  v-show="!showSinDatos && !loading && datosCita.fecha"
                  class="text-left"
                >
                  Seleccione la cita que desee asignar. Si las opciones listadas
                  a continuacin no es lo que buscas, te invitamos a utilizar
