                    >Citas Web Alianza Diagnstica</a
                  >
                </p>
                <v-subheader
                  v-show="showSinDatos && !loading"
                  class="text-center"
                  >Sin Datos</v-subheader
                >
                <v-progress-circular
                  v-show="loading"
                  indeterminate
                  color="primary"
                ></v-progress-circular>
                <v-list-item-group color="primary">
                  <v-list-item
                    v-for="(item, i) in citasDisponibles"
