            </v-card-text>
          </v-stepper-content>
          <v-stepper-content step="5" class="px-0">
            <v-card-text class="text-center pt-1 pb-0 px-0">
              <v-list dense>
                <v-list-item-group color="primary">
                  <v-list-item>
                    <v-list-item-content class="text-left">
                      <p>
                        Confirmacin de Cita para
                        <b>{{ afiliadoSel ? afiliadoSel.Nombres : "" }}</b>
                      </p>
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content class="text-left">
                      <v-list-item-subtitle
                        ><b>Mdico:</b>
