                          citaSel ? citaSel.medico : ""
                        }}</v-list-item-subtitle
                      >
                      <v-list-item-subtitle
                        ><b>Fecha:</b>
                        {{
                          citaSel ? citaSel.fechaCita : ""
                        }}</v-list-item-subtitle
                      >
                      <v-list-item-subtitle
                        ><b>Hora:</b>
                        {{
                          citaSel ? citaSel.horaCita : ""
                        }}</v-list-item-subtitle
                      >

                      <v-list-item-subtitle
                        ><b>Sede:</b>
                        {{ citaSel ? citaSel.ips : "" }}</v-list-item-subtitle
                      >
                      <v-list-item-subtitle
                        ><b>Direccin:</b>
                        {{
                          citaSel ? citaSel.dirIps : ""
                        }}</v-list-item-subtitle
                      >
                    </v-list-item-content>
                  </v-list-item>
                  <v-progress-circular
                    v-show="confirmandoCita"
                    indeterminate
                    color="primary"
                  ></v-progress-circular>
                  <v-list-item>
                    <v-alert
                      v-model="citaConfirmada"
                      dense
                      outlined
                      type="success"
                    >
                      Cita asignada exitosamente.
                    </v-alert>
                  </v-list-item>
                  <v-btn
                    v-show="citaConfirmada"
                    small
                    color="primary"
                    @click="sendMessageUser('ir_a_opciones_principales')"
                  >
                    Men Principal
                  </v-btn>
                  <v-btn
                    v-show="!citaConfirmada"
                    small
                    color="primary"
                    @click="confirmarCita()"
                  >
                    Confirmar
                  </v-btn>
                  <v-btn
                    v-show="!citaConfirmada"
                    small
                    text
                    @click="prevStep()"
                  >
                    Anterior
                  </v-btn>
                </v-list-item-group>
              </v-list>
            </v-card-text>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
      <v-alert v-model="alert" dense outlined type="error" dismissible>
        {{ errorValidate }}
      </v-alert> -->
    </v-card>
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex";
import moment from "moment";
import axios from "axios";
export default {
  props:['cancela'],
  data() {
    return {
      currentStep: 1,
      datosCita: {
        documento: null,
        tipoDoc: null,
        fecha: null,
        codEspecialidad: "",
        nomEspecialidad: "",
      },
      afiliadoSel: null,
      especialidadSel: null,
      beneficiarioSel: null,
      citaSel: null,
      citasDisponibles: [],
      grupoFamiliar: [],
      especialidades: [],
      tiposDoc: [
        // { value: null, text: "" },
        { value: "C", text: "Cdula de Ciudadana", id: 1 },
        { value: "T", text: "Tarjeta de Identidad", id: 2 },
        { value: "R", text: "Registro Civil", id: 3 },
        { value: "N", text: "Nit", id: 4 },
        { value: "E", text: "Cdula de Extranjera", id: 5 },
        { value: "CD", text: "Carnet Diplomtico", id: 11 },
        { value: "CN", text: "Certificado Nacido Vivo", id: 13 },
        { value: "J", text: "Personera Jurdica", id: 6 },
        { value: "M", text: "Menor de Edad", id: 7 },
        { value: "MS", text: "Menor Sin Identificacin", id: 8 },
        { value: "NU", text: "Nmero de Identificacin Personal", id: 9 },
        { value: "P", text: "Pasaporte", id: 10 },
        { value: "PE", text: "Permiso Especial de Permanencia", id: 14 },
        { value: "SC", text: "Salvoconducto", id: 12 },
      ],
      tokenAgenda: "",
      tokenAfiliado: "",
      tokenAuth: "",
      showDatepicker: false,
      showSinDatos: false,
      tmpCitasDisponibles: [],
      errorValidate: "",
      alert: false,
      loading: false,
      citaConfirmada: false,
    };
  },

  computed: {
    ...mapState([]),
  },
  async mounted() {
    // await this.getTokenAuth();
    // await this.getTokenAfiliado();
    // await this.getTokenAgenda();
  },
  methods: {
    ...mapActions(["sendMessageUser"]),
    getTokenAuth() {
      return new Promise((resolve, reject) => {
        let dataToSend = {
          PerfilUsuario: "IPS",
          TipoEmpleadorId: "N",
          EmpleadorId: "900038024",
          TipoDocumentoId: "C",
          DocumentoId: "915369141",
          Clave: "Alianza2021.",
          Aplicativo: "CITAS-ALIANZA",
        };
        axios
          .post(`http://186.116.13.121:91/proxy-citasweb/auth`, dataToSend)
          .then((response) => {
            if (response.data.Valido === true) {
              this.tokenAuth = response.data.Token;
              // console.log(this.tokenAuth);
            } else {
              console.error("Error de autenticacion");
              this.tokenAuth = null;
            }
            resolve();
          })
          .catch((error) => {
            console.error(error);
            this.tokenAuth = null;
            reject();
          });
      });
    },
    getTokenAgenda() {
      return new Promise((resolve, reject) => {
        let dataToSend = {
          Token: this.tokenAuth,
          aporigen: "CITAS-ALIANZA",
        };
        axios
          .post(
            `http://186.116.13.121:91/proxy-citasweb/agenda/getToken`,
            dataToSend
          )
          .then((response) => {
            this.tokenAgenda = response.data.Token;
            resolve();
          })
          .catch((error) => {
            console.error(error);
            this.tokenAgenda = null;
            reject(error);
          });
      });
    },
    getTokenAfiliado() {
      return new Promise((resolve, reject) => {
        let dataToSend = {
          Token: this.tokenAuth,
          origen: "CITAS-ALIANZA",
        };
        axios
          .post(
            `http://186.116.13.121:91/proxy-citasweb/afiliado/getToken`,
            dataToSend
          )
          .then(async (response) => {
            if (response.data.Valido === true) {
              this.tokenAfiliado = response.data.Token;
              resolve();
            } else {
              console.error(response.data.Error);
              this.tokenAfiliado = null;
              reject();
            }
          })
          .catch((error) => {
            console.error(error);
            this.tokenAfiliado = null;
            reject();
          });
      });
    },
    getGrupoFamiliar() {
      return new Promise((resolve, reject) => {
        let tipoIdentificacion = this.tiposDoc.find(
          (e) => e.value == this.datosCita.tipoDoc
        );
        let dataToSend = {
          BeneficiarioId: this.datosCita.documento,
          BeneficiarioTipoId: tipoIdentificacion.id,
        };
        axios
          .post(
            `http://186.116.13.121:91/proxy-citasweb/afiliado/grupoFamiliar`,
            dataToSend,
            {
              headers: {
                Authorization: this.tokenAfiliado,
              },
            }
          )
          .then((response) => {
            this.grupoFamiliar = response.data.map((el) => {
              return {
                ...el,
                value: el.BeneficiarioId,
                text: el.Nombres,
              };
            });
            this.afiliadoSel = response.data.find(
              (e) => e.BeneficiarioId == this.datosCita.documento
            );
            resolve();
          })
          .catch((error) => {
            console.error(error);
            reject();
          });
      });
    },
    getEspecialidades() {
      return new Promise((resolve, reject) => {
        let tipoIdentificacion = this.tiposDoc.find(
          (e) => e.value == this.datosCita.tipoDoc
        );
        let dataToSend = {
          numero_documento: this.datosCita.documento,
          tipo_documento: tipoIdentificacion.id,
        };
        axios
          .post(
            `http://186.116.13.121:91/proxy-citasweb/agenda/listar-especialidades`,
            dataToSend,
            {
              headers: {
                Authorization: this.tokenAgenda,
              },
            }
          )
          .then((response) => {
            this.especialidades = response.data.map((el) => {
              return {
                ...el,
                value: el.codespecialidad,
                text: el.especialidad,
              };
            });
            console.log(this.especialidades);
            resolve();
          })
          .catch((error) => {
            console.error(error);
            reject();
          });
      });
    },
    getCitasDisponibles() {
      return new Promise((resolve, reject) => {
        this.showDatepicker = false;
        let dataToSend = {
          documentoafiliado: this.datosCita.documento,
          tipo_documento: this.datosCita.tipoDocId,
          codespecialidad: this.datosCita.codEspecialidad,
          producto: 1,
          citasxmedico: 5,
          Fecha: moment(this.datosCita.fecha).format("MM-DD-YYYY"),
          horai: "null",
          mini: "null",
          horaf: "null",
          minf: "null",
          sede: "null",
          medico: "null",
          diassemana: "1,2,3,4,5,6,7",
          Oportunidad: true,
        };
        this.loading = true;
        axios
          .post(
            `http://186.116.13.121:91/proxy-citasweb/agenda/citas-disponibles`,
            dataToSend,
            {
              headers: {
                Authorization: this.tokenAgenda,
              },
            }
          )
          .then((response) => {
            this.loading = false;
            if (response.data[0].IPS === null) {
              this.citasDisponibles = [];
              this.showSinDatos = true;
              return;
            }
            this.tmpCitasDisponibles = response.data.filter(
              (e) =>
                moment(e.Fecha).format("MM-DD-YYYY") ==
                moment(this.datosCita.fecha).format("MM-DD-YYYY")
            );
            this.citasDisponibles = response.data
              .filter(
                (e) =>
                  moment(e.Fecha).format("MM-DD-YYYY") ==
                  moment(this.datosCita.fecha).format("MM-DD-YYYY")
              )
              .map((el) => {
                return {
                  idCita: el.IdCita,
                  medico: el.Medico,
                  fecha: `Fecha: ${moment(el.Fecha)
                    .locale("es")
                    .format("LL")}       Hora: ${moment(el.Fecha).format(
                    "hh:mm a"
                  )}`,
                  sede: "Sede: " + el.IPS,
                  direccion: "Direccin: " + el.DirIPS,
                };
              });
            if (this.citasDisponibles.length == 0) {
              this.showSinDatos = true;
            } else {
              this.showSinDatos = false;
            }
            resolve();
          })
          .catch((error) => {
            console.error(error);
            reject();
          });
      });
    },
    nextStep() {
      if (this.currentStep == 1) {
        if (this.datosCita.tipoDoc === null) {
          this.errorValidate = "El tipo de documento es requerido";
          this.alert = true;
          return;
        }
        if (this.datosCita.documento === null) {
          this.errorValidate = "El documento del afiliado es requerido";
          this.alert = true;
          return;
        }
        this.alert = false;
        this.getGrupoFamiliar().then(() => {
          this.currentStep = 2;
          return;
        });
      }
      if (this.currentStep == 2) {
        if (this.beneficiarioSel === null) {
          this.errorValidate = "El beneficiario es requerido";
          this.alert = true;
          return;
        }
        this.alert = false;
        this.getEspecialidades().then((rta) => {
          this.currentStep = 3;
          return;
        });
      }
      if (this.currentStep == 3) {
        if (this.especialidadSel === null) {
          this.errorValidate = "La especialidad es requerida";
          this.alert = true;
          return;
        }
        this.alert = false;
        this.currentStep = 4;
        return;
      }
      if (this.currentStep == 4) {
        this.currentStep = 5;
        return;
      }
      if (this.currentStep == 5) {
        this.currentStep = 6;
        return;
      }
    },
    prevStep() {
      this.currentStep -= 1;
    },
    seleccionarBeneficiario() {
      this.afiliadoSel = this.grupoFamiliar.find(
        (e) => e.BeneficiarioId == this.beneficiarioSel
      );
      this.datosCita.documento = this.afiliadoSel.BeneficiarioId;
      this.datosCita.tipoDoc = this.afiliadoSel.BeneficiarioTipoId;
      let tipoDoc = this.tiposDoc.find(
        (e) => e.value == this.afiliadoSel.BeneficiarioTipoId
      );
      this.datosCita.tipoDocId = tipoDoc.id;
    },
    seleccionarEspecialidad() {
      this.especialidadSel = this.especialidades.find(
        (e) => e.value == this.especialidadSel
      );
      this.datosCita.nomEspecialidad = this.especialidadSel.especialidad;
      this.datosCita.codEspecialidad = this.especialidadSel.codespecialidad;
    },
    asignarCita(itemCita) {
      let citaSeleccionada = this.tmpCitasDisponibles.find(
        (e) => e.IdCita == itemCita.idCita
      );
      this.citaSel = {
        fechaCita: moment(citaSeleccionada.Fecha)
          .locale("es")
          .format("LL"),
        horaCita: moment(citaSeleccionada.Fecha)
          .locale("es")
          .format("hh:mm a"),
        medico: citaSeleccionada.Medico,
        ips: citaSeleccionada.IPS,
        dirIps: citaSeleccionada.DirIPS,
        idCita: citaSeleccionada.IdCita,
      };
      this.nextStep();
    },
    confirmarCita() {
      this.confirmandoCita = true;
      let tmpCitaSeleccionada = this.tmpCitasDisponibles.find(
        (e) => e.IdCita == this.citaSel.idCita
      );
      let dataToSend = {
        dataCita: {
          numero_documento: this.datosCita.documento,
          tipo_documento: this.datosCita.tipoDocId,
          idCita: tmpCitaSeleccionada.IdCita,
          planSalud: 1,
          sede: tmpCitaSeleccionada.codsede,
          duracion: tmpCitaSeleccionada.Duracion,
          fechaCita: tmpCitaSeleccionada.Fecha,
          codespecialidad: this.datosCita.codEspecialidad,
        },
        dataToReport: {
          tipoDoc: this.datosCita.tipoDoc,
          documento: this.datosCita.documento,
          especialidad: this.datosCita.nomEspecialidad,
          origen: "CHATBOT",
        },
      };

      axios
        .post(
          `http://186.116.13.121:91/proxy-citasweb/agenda/asignar-cita`,
          dataToSend,
          {
            headers: {
              Authorization: this.tokenAgenda,
            },
          }
        )
        .then((response) => {
          this.confirmandoCita = false;
          this.citaConfirmada = true;
          console.log(response.data);
          resolve();
        })
        .catch((error) => {
          reject();
        });
    },
  },
};
</script>

<style scoped>
/* Alto de los inputs */
/* .v-input__control {
  height: 70px;
} */

.cb-campo-form {
  height: 60px;
}
</style>
