import store from '../store/index';
import axios from 'axios'

function iniciar(){
  resetChat();
  return saludo();
};

function mensaje(msg){
  // console.log('mensaje recibido',msg)
  let currentIntent = getCurrentIntent();
  if (!currentIntent) {
    return saludo();
  }
  switch (currentIntent) {
    case "saludo":
      return saludoRes(msg);
      break;
    case "authManejoDatos":
      return authManejoDatosRes(msg);
      break;
    case "mainOptions":
      return mainOptionsRes(msg);
      break;
    case "tomaMuestrasDom":
      return tomaMuestrasDomRes(msg);
      break;
    case "autorizacionesSt":
      return autorizacionesStRes(msg);
      break;
    case "tomaMuestrasCovidDom":
      return tomaMuestrasCovidDomRes(msg);
      break;
    case "planesComplementarios":
      return planesComplementariosRes(msg);
      break;
    case "programacionCitas":
      return programacionCitasRes(msg);
      break;
    case "informacionGeneral":
      return informacionGeneralRes(msg);
      break;
    case "resultadosDom":
      return resultadosDomRes(msg);
      break;
    case "solicitudCitas":
      return solicitudCitasRes(msg);
      break;
    case "solicitudHistoriaClinica":
      return solicitudHistoriaClinicaRes(msg);
      break;
    case "solicitudCitasSaludTotal":
      console.log('mensaje solicitud',msg)
      if(msg == 'ir_a_cancelar_citas'){
        return cancelarCitas(msg);
      } 
      if(msg == 'ir_a_citas'){
        return solicitudCitas(msg);
      } 
      return mainOptions(msg);
      break;
    case "opcionesCancelarCitas":
      return opcionesCancelarCitasRes(msg);
      break;
    case "opcionesTomaMuestras":
      return opcionesTomaMuestrasRes(msg);
      break;
    case "cancelarCitas":
      return cancelarCitasRes(msg);
      break;
    case "infoSedes":
      return infoSedesRes(msg);
      break;
    case "confirmacion":
      return confirmacionRes(msg);
      break;
    case "cancelar":
      return mainOptions(msg);
      break;
    default:
      return noEntendi();
      break;
  }
};

function resetChat() {
  localStorage.removeItem("currentIntent");
  localStorage.removeItem("nameUser");
};
function setCurrentIntent(intent) {
  localStorage.setItem("currentIntent", intent);
};

function getCurrentIntent(){
  return localStorage.getItem("currentIntent");
};

function setNameUser (name){
  localStorage.setItem("nameUser", name);
};

function getNameUser(){
  return localStorage.getItem("nameUser");
};

//Saludo del bot al usuario, y pide el nombre
function saludo() {
  setCurrentIntent("saludo");

  return {
    message:
      "Hola, soy Angela, tu asistente virtual de Alianza Diagnstica. Cul es tu nombre?",
  };
};

//Respuesta del usuario al saludo del bot, tomamos lo que escribi como el nombre
function saludoRes(nameUser){
  setNameUser(nameUser);
  return authManejoDatos();
};

//Muestra al usuario el mensaje para autorizar o no el tratamiento de datos
function authManejoDatos(){
  setCurrentIntent("authManejoDatos");
  let user = getNameUser();
  return {
    message: `Hola, ${user}, es un placer que nos visites. Aceptas el tratamiento de tus datos conforme a la poltica de privacidad consignada en esta pgina web?`,
    option: "mostrar_autorizacion_datos",
  };
};

//Recibimos la respuesta del usuario si acepta o no el tratamiento de datos
function authManejoDatosRes(rta){
  if (rta == "SI" || rta == "Si" || rta == "si") {
    return mainOptions();
  }
  if (rta == "NO" || rta == "No" || rta == "no") {
    return noManejoDatos();
  }
};

//Respuesta del bot si el usuario no acepta el tratamiento de datos
function noManejoDatos(){
  setCurrentIntent("noManejoDatos");
  return {
    message:
      "De acuerdo, hemos borrado los datos que ingresaste, por lo tanto no puedo seguir ayudndote. Gracias por ponerte en contacto con nosotros. Te invitamos a seguir explorando nuestro sitio web.",
    option: "mostrar_rechaza_datos",
  };
};

//Mostrarle al usuario las opciones principales
function mainOptions(){
  setCurrentIntent("mainOptions");
  let user = getNameUser();
  let mensaje = `${user}, en qu te puedo ayudar?`;
  if(store.state.programa_premium != ''){
    mensaje = `Para el programa ${store.state.programa_premium} contamos con las siguientes opciones:`
  }
  return {
    message: mensaje,
    option: "mostrar_opciones_principales",
  };
};

//Respuesta del usuario a las opciones principales
function mainOptionsRes(rta){
  switch (rta) {    
    case "ir_a_muestras_covid":
      return tomaMuestrasCovidDom();
      break;
    case "ir_a_planes_complementarios":
      return planesComplementarios();
      break;
    
    case "ir_a_programacion_citas":
      return programacionCitas();
      break;  
    case "ir_a_info_general":
      return informacionGeneral();
      break;      
    case "ir_a_cancelar_citas":
      return opcionesCancelarCitas();
      break;
    case "ir_a_salir":
      return salir();
    case "ir_a_opciones_principales":
      return mainOptions();
    case "ir_a_autorizaciones_st":
      return autorizacionesSt();
      break;
    case "ir_a_historia_clinica":
      return solicitudHistoriaClinica();
      break;
    case "ir_a_opciones_toma_muestras":
      return opcionesTomaMuestras();
      break;
    default:
      return noEntendi();
      break;
  }
};

