//Respuesta del bot al recibir los datos enviados por los formularios para muestras, resultados y citas
function confirmacion(){
  setCurrentIntent("confirmacion");
  let user = getNameUser();
  return {
    message: `${user}, su solicitud fue recibida. Uno de nuestros agentes se pondr en contacto en las siguientes 48 horas hbiles. Le puedo colaborar en algo ms?`,
    option: "mostrar_opcion_salir",
  };
};
function confirmacionAutorizaciones(){
  setCurrentIntent("confirmacion");
  let user = getNameUser();
  return {
    message: `${user}, nos permitimos confirmar que hemos recibido su solicitud. <br><br>En el transcurso de dos (2) das hbiles de recibida su solicitud, se enviar respuesta al correo electrnico notificado. <br><br>En caso de no enviar la documentacin completa, se notificar al correo electrnico y el tiempo de respuesta slo iniciar hasta que se reciba la informacin correspondiente. <br><br>Le puedo colaborar en algo ms?`,
    option: "mostrar_opcion_salir",
  };
};

//Respuesta del bot al recibir los datos enviados por los formularios para muestras, resultados y citas
function confirmacionRes(rta){
  if (rta == "ir_a_opciones_principales") {
    return mainOptions();
  }
  if (rta == "ir_a_salir") {
    return salir();
  }
};

//Respuesta del bot al solicitar salir de la conversacin
function salir(){
  resetChat();
  return {
    message: "Gracias por utilizar nuestros servicios.",
    option: "mostrar_volver_empezar",
  };
};

//Respuesta del bot cuando ninguna respuesta por parte del usuario se ha entendido
function noEntendi(){
  return {
    message: "No entend lo que dijiste.",
  };
};

