<template>
  <v-container>
    <v-card elevation="5">
      <v-card-text class="text-center pt-1 pb-0">
        <h3>
          Trmites rdenes Mdicas <br>Salud Total
        </h3>
      </v-card-text>
      <v-card-text class="text-center">
        <v-form ref="form" v-model="valid">
          <v-text-field
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.nombre"
            label="Nombre Completo"
            :rules="[(v) => !!v || 'Debe indicar el nombre']"
          ></v-text-field>
          <v-text-field
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.documento"
            label="Documento de Identidad"
            :rules="[(v) => !!v || 'Debe indicar documento de identidad']"
            type="number"
          ></v-text-field>
          <v-text-field
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.telefono"
            label="Telfono de Contacto"
            :rules="[(v) => !!v || 'Debe indicar un telfono de contacto']"
            type="number"
          ></v-text-field>
          <!-- <v-text-field
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.direccion"
            label="Direccin, Barrio y Municipio"
            :rules="[(v) => !!v || 'La direccin es requerida']"
          ></v-text-field> -->
          <v-text-field
            type="email"
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.correo"
            label="Correo Electrnico"
            :rules="emailRules"
          ></v-text-field>  
          <div class="mt-0 text-left" style="color: red" v-show="showErrorCorreo && datos.correo == ''">Debe ingresar un correo</div>    
          <v-text-field
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.observaciones"
            label="Observaciones"
          ></v-text-field>
          <div class="form-group text-left mb-4">
            <label for="attachFiles" class="mb-2">Adjuntar rden Mdica y/o Histria Clnica</label>
            <input type="file" class="form-control-file" id="attachFiles" multiple :disabled="!showTramitarButton">
            <div class="my-0" v-show="showErrorAttach"><br><label for="" style="color: red">Debe adjuntar un documento</label> </div>                        
          </div>     
          <!-- <input
            class="cb-campo-form"  
            type="file"                   
            label="Click para adjuntar rden mdica"
            v-model="datos.ordenMedica"
            :rules="[
              (v) => !!v || 'Debe adjuntar la rden mdica',
            ]"
          /> -->
          <!-- <input
            class="cb-campo-form"
            type="file"                      
            label="Click para adjuntar Historia Clnica"
            v-model="datos.historiaClinica"
          /> -->
          <v-card-text class="text-left text-justify px-0 py-0 mb-3">
            <span style="color:red;">**</span> Seor usuario, recuerde que el envo de la rden mdica es obligatorio
          </v-card-text>  
          <v-btn color="primary" @click="tramitar()" small
             v-if="showTramitarButton">Enviar</v-btn>
          |
          <v-btn
            color="success"
            @click="sendMessageUser('ir_a_opciones_principales')"
            small
            >Men Anterior</v-btn
          >
        </v-form>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  data() {
    return {
      valid: false,
      datos: {
        nombre: "",
        documento: "",
        telefono: "",
        direccion: "",
        correo: "",
        observaciones: "",
        entidad_salud: "",
        ordenMedica: null,
        historiaClinica: null
      },
      showTramitarButton: true,
      showErrorCorreo: false,
      showErrorAttach: false,
      emailRules: [ v => !v || (v.match(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)) || 'El correo no es vlido']
    };
  },
  computed: {
    ...mapState(["imageAgent", "emailToAutorizacionesSt"]),
  },
  methods: {
    ...mapActions(["sendMessageUser", "sendEmail"]),
    async tramitar() {
      this.showErrorAttach =false;
      this.showErrorCorreo = false;
      if(this.datos.correo == ''){
        this.showTramitarButton = true;
        this.showErrorCorreo = true;
        return;
      }
      if (!this.$refs.form.validate()) {
        this.showTramitarButton = true;
        console.log("no valido");
        return;
      }
      if(document.getElementById('attachFiles').files.length == 0){
        this.showTramitarButton = true;
        this.showErrorAttach = true;
        return;
      }
      this.showTramitarButton = false;
      const datosEmail = `
        <div>
          <table>
            <tbody>
              <tr>
                <td><b>Nombre Completo</b></td>
                <td>${this.datos.nombre}</td>
              </tr>
              <tr>
                <td><b>Documento de Identificacin</b></td>
                <td>${this.datos.documento}</td>
              </tr>
              <tr>
                <td><b>Telfono</b></td>
                <td>${this.datos.telefono}</td>
              </tr>
              <tr>
                <td><b>Correo Electrnico</b></td>
                <td>${this.datos.correo}</td>
              </tr>
              <tr>
                <td><b>Observaciones</b></td>
                <td>${this.datos.observaciones}</td>
              </tr>
            </tbody>
          </table>
        </div>
      `;
      
      let archivos = document.getElementById("attachFiles");    
      this.sendEmail({
        to: this.emailToAutorizacionesSt,
        message: datosEmail,
        subject: `Angela Autorizaciones - ${this.datos.nombre}`,
        intent: "Formulario Autorizaciones",
        docIdentidad: this.datos.documento,
        files: archivos.files,
        autorizaciones: true
      }).then(rta => {
        const plantillaAutoRespuesta = `
          <p>Cordial saludo,</p>

          <p>Seor usuario, nos permitimos confirmar que hemos recibido su solicitud; recuerde que para realizar el trmite, debe adjuntar orden mdica y/o Historia Clnica donde se evidencie la remisin de los servicios que requiere, as como sus datos completos: Nombres y Apellidos, tipo y nmero de documento, correo electrnico y nmeros de contacto.</p>

          <p>En el transcurso de dos (2) das hbiles de recibida su solicitud, se enviar respuesta al correo electrnico notificado.</p> 

          <p>En caso de no enviar la documentacin completa, se notificar al correo electrnico y el tiempo de respuesta slo iniciar hasta que se reciba la informacin correspondiente.</p>

          <p>Gracias por su atencin.</p>

          <p>
          <b>Servicio al Cliente</b>
          <br>
          <b>Alianza Diagnstica S.A.</b>
          </p>
        `
        this.sendEmail({
          to: this.datos.correo,
          message: plantillaAutoRespuesta,
          subject: `Angela Autorizaciones - ${this.datos.nombre} - Radicado # ${rta.data.numRadicado}`,
          intent: "Formulario Autorizaciones",
          docIdentidad: "",
          files: [],
          autorizaciones: false,
          autoRta: true
        });
      })

      
    },
  }
};
</script>

<style scoped> 
/* Alto de los inputs */
/* .v-input__control {
  height: 70px;
} */

.cb-campo-form {
  height: 60px;
}
</style>