<template>
  <v-container>
    <v-card elevation="5">
      <v-card-text class="text-center pt-1 pb-0">
        <h3>Formulario Solicitud Historia Clnica</h3>
      </v-card-text>
      <v-card-text class="text-center">
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.nombre"
            label="Nombre Completo"
            :rules="[(v) => !!v || 'Debe indicar el nombre']"
          ></v-text-field>
          <v-select
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.tipoDoc"
            :items="tipoDocumentos"
            return-object
            label="Tipo Identificacin"
            :rules="[(v) => !!v || 'El servicio a solicitar es requerido']"
          ></v-select>
          <v-text-field
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.documento"
            label="Documento de Identidad"
            :rules="[(v) => !!v || 'Debe indicar documento de identidad']"
            type="number"
          ></v-text-field>
          <v-text-field
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.telefono"
            label="Telfono o Celular de Contacto"
            :rules="[(v) => !!v || 'Debe indicar un telfono de contacto']"
            type="number"
          ></v-text-field>
          <v-text-field
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.email"
            label="Email"
            :rules="[
              (v) => !!v || 'Debe indicar un email',
              (v) => /.+@.+\..+/.test(v) || 'Debe ingresar un email vlido',
            ]"
          ></v-text-field>
          <!-- <v-datetime-picker
            v-model="datos.fecha_hora"
            :text-field-props="textFieldProps"
            :date-picker-props="dateProps"
            time-format="hh:mm:ss a"
            label="Fecha y Hora de la Cita"
            clearText="Cancelar"
            okText="Aceptar"
          >
            <template v-slot:dateIcon>
              <v-icon>mdi-calendar</v-icon>
            </template>
            <template v-slot:timeIcon>
              <v-icon>mdi-timer</v-icon>
            </template>
          </v-datetime-picker> -->
          <v-select
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.servicio"
            :items="servicios"
            return-object
            label="Servicio de la Historia"
            :rules="[(v) => !!v || 'El servicio a solicitar es requerido']"
          ></v-select>
          <v-textarea
            v-model="datos.observaciones"
            label="Observaciones"
            filled
            auto-grow
          ></v-textarea>
          <!-- <v-text-field
            class="cb-campo-form"
            dense
            outlined
            v-model="datos.entidad_salud"
            label="EPS que lo remite"
            :rules="[(v) => !!v || 'Debe indicar la EPS que lo remite']"
          ></v-text-field> -->

          <v-btn color="primary" @click="solicitarHistoria()" small>Solicitar</v-btn
          >|
          <v-btn
            color="success"
            @click="sendMessageUser('ir_a_opciones_principales')"
            small
            >Men Anterior</v-btn
          >
        </v-form>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex";
import moment from "moment";
import serviciosHistoriaClinica from "../db/serviciosHistoriaClinica.json";
import tipoDocumentosJson from "../db/tipoDocumentos.json";
export default {
  data() {
    return {
      textFieldProps: {
        style: "font-size: 0.8em;",
        dense: true,
        outlined: true,
        rules: [(v) => !!v || "Debe indicar una Fecha y Hora para la cita"],
      },
      dateProps: {
        headerColor: "primary",
        min: moment(new Date())
          .add(1, "d")
          .format("YYYY-MM-DD"),
      },
      valid: false,
      datos: {
        nombre: "",
        documento: "",
        telefono: "",
        email: "",
        fecha_hora: "",
        servicio: "",
        entidad_salud: "",
        observaciones: ""
      },
      servicios: [],
      tipoDocumentos: []
    };
  },

  computed: {
    ...mapState(["imageAgent", "emailToCitas"]),
  },
  mounted(){
    this.servicios = serviciosHistoriaClinica.map(e => e.nombre).sort()
    this.tipoDocumentos = tipoDocumentosJson.map(e => e.nombre).sort()
  },
  methods: {
    ...mapActions(["sendMessageUser", "sendEmail"]),
    formatearFecha(date) {
      return moment(date)
        .locale("es")
        .format("dddd D MMMM [de] YYYY hh:mm a");
    },
    getEmailDestino(servicio){
      let found = serviciosHistoriaClinica.find(e => e.nombre == servicio)
      if(found){
        return found.email
      }
      return ''
    },
    getTipoDoc(tipo){
      let found = tipoDocumentosJson.find(e => e.nombre == tipo)
      if(found){
        return found.codigo
      }
      return ''
    },
    solicitarHistoria() {
      if (!this.$refs.form.validate()) {
        console.log("no valido");
        return;
      }
      // <tr>
      //   <td><b>Fecha y Hora</b></td>
      //   <td>${this.formatearFecha(this.datos.fecha_hora)}</td>
      // </tr>
      const datosEmail = `
	       <div>
	         <table>
	           <tbody>
	             <tr>
	               <td><b>Nombre Completo</b></td>
	               <td>${this.datos.nombre}</td>
	             </tr>
               <tr>
	               <td><b>Tipo de Identificacin</b></td>
	               <td>${this.datos.tipoDoc}</td>
	             </tr>
	             <tr>
	             <tr>
	               <td><b>Documento de Identificacin</b></td>
	               <td>${this.datos.documento}</td>
	             </tr>
	             <tr>
	               <td><b>Telfono</b></td>
	               <td>${this.datos.telefono}</td>
	             </tr>
	             <tr>
	               <td><b>Email</b></td>
	               <td>${this.datos.email}</td>
	             </tr>
               <tr>
	               <td><b>Servicio</b></td>
	               <td>${this.datos.servicio}</td>
	             </tr>	 
               <tr>
	               <td><b>Observaciones</b></td>
	               <td>${this.datos.observaciones}</td>
	             </tr>           
	           </tbody>
	         </table>
	       </div>
	     `;
             
      this.sendEmail({
        to: this.getEmailDestino(this.datos.servicio),
        message: datosEmail,
        subject: "Solicitud de Historia Clnica - Angela",
        intent: "Formulario Historia Clnica"
      });
    },
  },
};
</script>

<style scoped>
/* Alto de los inputs */
/* .v-input__control {
  height: 70px;
} */

.cb-campo-form {
  height: 60px;
}
</style>
